const className = 'imageColLayout';

// Check if images loaded and resolve it in a Promise

const checkImg = path =>
    new Promise(resolve => {
        const img = new Image();
        img.onload = () => resolve({ path, status: 'ok' });
        img.onerror = () => resolve({ path, status: 'error' });

        img.src = path;
    });

function init(elem) {
    const columns = 4,
        images = [].slice.call(elem.querySelectorAll('img')).map(i => i.src);

    // Promise object
    const loadImg = (...paths) => Promise.all(paths.map(checkImg));

    // When resolve calculate elements height
    loadImg(images).then(() => {
        const items = [].slice.call(
            elem.querySelectorAll('.imageColLayout--card')
        );
        const total = items.map(el => {
            return el.offsetHeight;
        });

        let add = total.reduce((a, c) => a + c, 0);

        let rows = (items.length + 1) / columns;
        let a = add / (items.length + 1);
        elem.style.setProperty('--h', a * Math.ceil(rows) + 'px');
    });
}
export default {
    launchFn: init,
    launchQuery: `.${className}`,
};
